<h1 id="消息" tabindex="-1"><a class="header-anchor" href="#消息" aria-hidden="true">#</a> 消息</h1>
<p>公众号消息分为 <a href="https://developers.weixin.qq.com/doc/offiaccount/Message_Management/Passive_user_reply_message.html" target="_blank" rel="noopener noreferrer"><strong>服务端被动回复消息</strong><OutboundLink/></a> 和 <a href="https://developers.weixin.qq.com/doc/offiaccount/Message_Management/Service_Center_messages.html" target="_blank" rel="noopener noreferrer"><strong>客服消息</strong><OutboundLink/></a> 两个场景。</p>
<p>需要注意的是两个场景的消息虽然类似，但是结构却有些差异，比如服务端使用 XML 结构，而客服消息使用 JSON 结构，且同样类似的消息类型，结构和名称都有些许差异，在使用时请勿混淆。</p>
<h2 id="服务端消息结构" tabindex="-1"><a class="header-anchor" href="#服务端消息结构" aria-hidden="true">#</a> 服务端消息结构</h2>
<p>当你接收到用户发来的消息时，可能会提取消息中的相关属性，参考：</p>
<p>请求消息基本属性(以下所有消息都有的基本属性)：</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `ToUserName`    接收方帐号（该公众号 ID）
  - `FromUserName`  发送方帐号（OpenID, 代表用户的唯一标识）
  - `CreateTime`    消息创建时间（时间戳）
  - `MsgId`        消息 ID（64位整型）
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="文本" tabindex="-1"><a class="header-anchor" href="#文本" aria-hidden="true">#</a> 文本：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`  text
  - `Content`  文本消息内容
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="图片" tabindex="-1"><a class="header-anchor" href="#图片" aria-hidden="true">#</a> 图片：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`  image
  - `MediaId`  图片消息媒体id，可以调用多媒体文件下载接口拉取数据。
  - `PicUrl`   图片链接
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="语音" tabindex="-1"><a class="header-anchor" href="#语音" aria-hidden="true">#</a> 语音：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`        voice
  - `MediaId`        语音消息媒体id，可以调用多媒体文件下载接口拉取数据。
  - `Format`         语音格式，如 amr，speex 等
  - `Recognition`  * 开通语音识别后才有
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><blockquote>
<p>{warning} 请注意，开通语音识别后，用户每次发送语音给公众号时，微信会在推送的语音消息XML数据包中，增加一个 <code>Recongnition</code> 字段</p>
</blockquote>
<h3 id="视频" tabindex="-1"><a class="header-anchor" href="#视频" aria-hidden="true">#</a> 视频：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`       video
  - `MediaId`       视频消息媒体id，可以调用多媒体文件下载接口拉取数据。
  - `ThumbMediaId`  视频消息缩略图的媒体id，可以调用多媒体文件下载接口拉取数据。
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="小视频" tabindex="-1"><a class="header-anchor" href="#小视频" aria-hidden="true">#</a> 小视频：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`     shortvideo
  - `MediaId`     视频消息媒体id，可以调用多媒体文件下载接口拉取数据。
  - `ThumbMediaId`    视频消息缩略图的媒体id，可以调用多媒体文件下载接口拉取数据。
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="事件消息" tabindex="-1"><a class="header-anchor" href="#事件消息" aria-hidden="true">#</a> 事件消息</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`     event
  - `Event`       事件类型 （如：subscribe(订阅)、unsubscribe(取消订阅) ...， CLICK 等）
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h4 id="扫描带参数二维码事件" tabindex="-1"><a class="header-anchor" href="#扫描带参数二维码事件" aria-hidden="true">#</a> 扫描带参数二维码事件</h4>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `EventKey`    事件KEY值，比如：qrscene_123123，qrscene_为前缀，后面为二维码的参数值
  - `Ticket`      二维码的 ticket，可用来换取二维码图片
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h4 id="上报地理位置事件" tabindex="-1"><a class="header-anchor" href="#上报地理位置事件" aria-hidden="true">#</a> 上报地理位置事件</h4>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `Latitude`    23.137466   地理位置纬度
  - `Longitude`   113.352425  地理位置经度
  - `Precision`   119.385040  地理位置精度
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h4 id="自定义菜单事件" tabindex="-1"><a class="header-anchor" href="#自定义菜单事件" aria-hidden="true">#</a> 自定义菜单事件</h4>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `EventKey`    事件KEY值，与自定义菜单接口中KEY值对应，如：CUSTOM_KEY_001, www.qq.com
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="地理位置" tabindex="-1"><a class="header-anchor" href="#地理位置" aria-hidden="true">#</a> 地理位置：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`     location
  - `Location_X`  地理位置纬度
  - `Location_Y`  地理位置经度
  - `Scale`       地图缩放大小
  - `Label`       地理位置信息
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="链接" tabindex="-1"><a class="header-anchor" href="#链接" aria-hidden="true">#</a> 链接：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`      link
  - `Title`        消息标题
  - `Description`  消息描述
  - `Url`          消息链接
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="文件" tabindex="-1"><a class="header-anchor" href="#文件" aria-hidden="true">#</a> 文件：</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  - `MsgType`      file
  - `Title`        文件名
  - `Description`  文件描述，可能为null
  - `FileKey`      文件KEY
  - `FileMd5`      文件MD5值
  - `FileTotalLen` 文件大小，单位字节
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="客服消息结构" tabindex="-1"><a class="header-anchor" href="#客服消息结构" aria-hidden="true">#</a> 客服消息结构</h2>
<h3 id="发送文本消息" tabindex="-1"><a class="header-anchor" href="#发送文本消息" aria-hidden="true">#</a> 发送文本消息</h3>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
    <span class="token property">"touser"</span><span class="token operator">:</span><span class="token string">"OPENID"</span><span class="token punctuation">,</span>
    <span class="token property">"msgtype"</span><span class="token operator">:</span><span class="token string">"text"</span><span class="token punctuation">,</span>
    <span class="token property">"text"</span><span class="token operator">:</span>
    <span class="token punctuation">{</span>
         <span class="token property">"content"</span><span class="token operator">:</span><span class="token string">"Hello World"</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h3 id="图片消息" tabindex="-1"><a class="header-anchor" href="#图片消息" aria-hidden="true">#</a> 图片消息</h3>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
    <span class="token property">"touser"</span><span class="token operator">:</span><span class="token string">"OPENID"</span><span class="token punctuation">,</span>
    <span class="token property">"msgtype"</span><span class="token operator">:</span><span class="token string">"image"</span><span class="token punctuation">,</span>
    <span class="token property">"image"</span><span class="token operator">:</span>
    <span class="token punctuation">{</span>
      <span class="token property">"media_id"</span><span class="token operator">:</span><span class="token string">"MEDIA_ID"</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h3 id="语音消息" tabindex="-1"><a class="header-anchor" href="#语音消息" aria-hidden="true">#</a> 语音消息</h3>
