</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><h3 id="图文消息-点击跳转到图文消息页面" tabindex="-1"><a class="header-anchor" href="#图文消息-点击跳转到图文消息页面" aria-hidden="true">#</a> 图文消息（点击跳转到图文消息页面）</h3>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
    <span class="token property">"touser"</span><span class="token operator">:</span><span class="token string">"OPENID"</span><span class="token punctuation">,</span>
