  <span class="token property">"msgmenu"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
    <span class="token property">"head_content"</span><span class="token operator">:</span> <span class="token string">"您对本次服务是否满意呢? "</span>
    <span class="token property">"list"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
      <span class="token punctuation">{</span>
        <span class="token property">"id"</span><span class="token operator">:</span> <span class="token string">"101"</span><span class="token punctuation">,</span>
